class SearchBar extends React.Component {
  constructor(props) {
    super(props);
    
    this.state = {
      search: [],
	  summ: 0,
	  counts: 0,
	  basket: false,
	  phone: (localStorage.getItem('phone'))?localStorage.getItem('phone'):'',
	  name: (localStorage.getItem('name'))?localStorage.getItem('name'):'',
	  phonevalid: (localStorage.getItem('phone'))?true:false,
	  showPopup: false,
	  reset: false,
	  activeLink: null
    }
	this.all = this.all.bind(this);
	this.baskets = this.baskets.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  all(){
		let count=0,
		counts=0,
		summ=0;
		courses.forEach((element, index, array) => 
			(
			count=+localStorage.getItem('count['+index+']'),
			element.count=count,
			summ+=+(element.price*count),
			counts+=+count
			)
		);
			this.setState({counts: counts});
			this.setState({summ: summ});
  }  
  baskets(e){
	  if(this.state.basket){if(e>0){return 'showinbasket';}else{return 'hideinbasket';}}else{return '';}
  }
  
	componentWillMount(){
		let arr=[];
		courses.forEach((element, i, array) => 
			(			
			arr.push((localStorage.getItem('count['+i+']'))?localStorage.getItem('count['+i+']'):0)
			)
		);
		this.setState({quantitie: arr});
		
		
		this.all();
	}
	
	
	  handleSubmit(event) {
		  event.preventDefault();
		  if(this.state.name.length<3){alert(this.state.name+' - Слишком короткое имя');return false};
		  if(!this.state.phonevalid){alert('Введите ваш номер телефона');return false};
			localStorage.setItem('name', this.state.name);
			localStorage.setItem('phone', this.state.phone);
			this.togglePopup();
			//alert('Отправленное имя: ' + this.state.name+'/'+this.state.name.length+' phone: '+this.state.phone);
  }  
  togglePopup() {
    this.setState({
      showPopup: !this.state.showPopup
    });
  }
  reset(){
	  this.setState({reset: true});
  }
  
  showtext(i){
	i=(i===this.state.activeLink)?null:i;
	this.setState({ activeLink: i });
  }
	
	/*mobilevalidate(text) {
    const reg = /^[0]?[789]\d{9}$/;
    if (reg.test(text) === false) {
      this.setState({
        mobilevalidate: false,
        telephone: text,
      });
      return false;
    } else {
      this.setState({
        mobilevalidate: true,
        telephone: text,
        message: '',
      });
      return true;
    }
  }*/  
  onClick = (e) => {
/*	const parent = e.target.parentNode.getBoundingClientRect();
	const element = e.target.getBoundingClientRect();

	const x = element.left - parent.left;
	const y = element.top - parent.top;
*/
	//alert('x'+x+' y'+y+' window'+window.top);
};
/*
callbackAnimateElement4() {  
    alert('callback animate of 4 element');
  }*/

  render() {
	  document.body.style.overflow = (this.state.activeLink)?'hidden':'auto';
    let options;
    if (this.state.search.length) {
      const searchPattern = new RegExp(this.state.search.map(term => `(?=.*${term})`).join(''), 'i');
      options = courses.filter(option => 
        option.name.match(searchPattern)
      );
    } else {
      options = courses;
    }
	if(this.state.phonevalid)localStorage.setItem('phone', this.state.phone);
	if(this.state.reset){
		options = [];
		this.all();
		this.setState({reset: false});
	};

//quantitie:{JSON.stringify(this.state.quantitie)}
//Введите ваш номер телефона:{this.state.phone} {(this.state.phonevalid)? 'true' : 'false'}Введите вае имя:{this.state.name} /
/*
	  <section>
          <ScrollEffect key="scr1" id="scr1" className="element" animate="bounceInUp" callback={this.callbackAnimateElement4.bind(this)}> { }
            <span>4</span>
          </ScrollEffect>
        </section>
	  */
    return (
      <div id="shop">
	  

	  
	  <div className={this.state.showPopup?'popup':'hide'}>
        <div className='popup_inner'>
          Ваши контакты
        <button onClick={this.togglePopup.bind(this)}></button>
		      <form onSubmit={this.handleSubmit}>
<div className="wrap-input100" >
<input className='input100' type="text" value={this.state.name} placeholder="Ваше имя" onChange={(e) => this.setState({name: e.target.value.replace(/[^ a-zа-яё\d]/ui, '')})}/>
<span className="focus-input100 userinput" ></span>
</div>
<div className="wrap-input100" >
<InputMask {...this.props} mask="+38\ (999) 999-99-99" placeholder="Ваш номер телефона" maskChar="_" value={this.state.phone} onChange={(e) => this.setState({phone: e.target.value.replace(/[^+\d]/g, ''), phonevalid:(e.target.validity.valid&&(e.target.value.replace(/[^+\d]/g, '').length===13))?true:false})} className="input100"/>
<span className="focus-input100 phoneinput" ></span>
</div>
        <input className="footer_button" type="submit" value="Сохранить" />
      </form>
        </div>
		  </div>
          

 
		  <div className="top">
  <button onClick={this.togglePopup.bind(this)} className='user'></button> 
			<div className="search">
			  <input type="text" className={(this.state.search.length)?'notempty':''} value={this.state.search} placeholder="Искать здесь..." onChange={(e) => this.setState({search: e.target.value.replace(/[^ a-zа-яё\d]/ui, '').split(' ')})}/>
			  <button className={(this.state.search.length)?'notempty':''} onClick={(e) => this.setState({search: ''})+'return false'} ></button>
			</div>
			<button onClick={(e) => this.setState({basket: !this.state.basket})+'return false'} className={'basket '+(this.state.basket ? 'back' : '')+' '+((this.state.counts>0)?'':'hide')} > </button>
		</div>
        <ul className="list" onClick={this.onClick}>
            {options.map((option, i) => 
			  <li key={option.name+i} className={this.baskets(option.count)+' '+(i === this.state.activeLink ? 'active_item' : '')+'  '+window.innerHeight}> <div onClick={(e) => this.showtext(i)} className={'img '+this.baskets(option.count)} style={{backgroundImage: 'url(./img/'+option.img+'.jpg)'}} ></div><div className='img_title' ><h2 className="link" onClick={(e) => this.showtext(i)}>{option.name+' '+option.price+'₴'} </h2>

			  <Count key={option.id} id={option.id} courses={courses} all={this.all} /><div className="text_list">{option.text}</div></div></li>
            )}
        </ul>
		<div className={'footer '+((this.state.counts>0)?'':'hide')}><SendBar courses={courses} counts={this.state.counts} summ={this.state.summ} errPopup={this.togglePopup.bind(this)} reset={this.reset.bind(this)} /></div>
      </div>
    )
  }
}

ReactDOM.render(
  <React.StrictMode>
    <App />
	<SearchBar />
  </React.StrictMode>,
  document.getElementById('root')
);

